<script>
import { TYPE_STRING } from "./input_type_string.js";
export default {
  name: "FormTableItem",
  props: {
    item: {
      required: true,
      type: Object
    },
    model: {
      required: true
      /* type: Object */
    },
    info: String
  },
  mounted() {
    let item = this.item;
    if (item.data) {
      console.log();
    } else if (item.bind) {
      let key = `panel_${item.bind.id}`;
      item.data = JSON.parse(localStorage.getItem(key));
    } else {
      console.log("no data no bind", item);
    }
  },
  data() {
    return { TYPE_STRING };
  },
  computed: {
    textareaRows() {
      return this.item.rows || 2;
    }
  },
  methods: {
    c(...args) {
      console.log.apply(console, args);
    },
    handleAvatarSuccess(res, item) {
      this.$emit("eventAvatarUploadSucess", res, item);
    },
    isType(typeString) {
      if (!this.item) return false;
      if (this.item.mutiple) {
        return "textarea" === typeString;
      }
      return this.item.type === typeString;
    }
  },
  components: {
    CAvatar: () =>
      import(/* webpackChunkName: "AvatarUpload" */ "@c/AvatarUpload.vue"),
    CInputSearch: () =>
      import(/* webpackChunkName: "CInputSearch" */ "@c/component-InputSearch/InputSearch.vue")
  }
};
</script>
<template>
  <!-- input  http://element-cn.eleme.io/#/zh-CN/component/input -->
  <el-form-item v-if="isType(TYPE_STRING.INPUT)" :label="item.label" :prop="item.prop">
    <el-input v-model="model[item.prop]" :disabled="Boolean(item.readonly)"></el-input>
  </el-form-item>
  <!-- input -->
  <!-- select http://element-cn.eleme.io/#/zh-CN/component/select -->
  <el-form-item v-else-if="isType(TYPE_STRING.SELECT )" :label="item.label" :prop="item.prop">
    <el-select v-model="model[item.prop]" :placeholder="item.placeholder">
      {{c(item)}}
      <el-option
        v-for="d in item.data"
        :key="d.value"
        :label="d.text"
        :value="d.value"
        :disabled="Boolean(item.readonly)"
      ></el-option>
    </el-select>
  </el-form-item>
  <!-- select -->
  <!-- search -->
  <el-form-item v-else-if="isType(TYPE_STRING.SEARCH )" :label="item.label" :prop="item.prop">
    <c-input-search v-model="model[item.prop]" :options="item"></c-input-search>
  </el-form-item>
  <!-- search -->
  <!-- switch -->
  <el-form-item v-else-if="isType(TYPE_STRING.SWITCH )" :label="item.label" :prop="item.prop">
    <el-switch v-model="model[item.prop]" :active-text="item.active" :inactive-text="item.inactive"></el-switch>
  </el-form-item>
  <!-- switch -->
  <!-- avatar -->
  <el-form-item
    class="avatar-wrapper"
    v-else-if="isType(TYPE_STRING.AVATAR )"
    :label="item.label"
    :prop="item.prop"
  >
  {{c("item",item)}}
    <c-avatar
      v-model="model[item.id||item.prop]"
      :options="item"
      @eventAvatarUploadSucess="handleAvatarSuccess"
    ></c-avatar>
    <!-- <div></div> -->
    <!-- <el-input :data-info="JSON.stringify(item)" v-model="model[item.id||item.prop]"></el-input> -->
  </el-form-item>
  <!-- avatar -->
  <!-- checkbox -->
  <el-form-item v-else-if="isType(TYPE_STRING.CHECKBOX )" :label="item.label" :prop="item.prop">
    <el-checkbox v-model="model[item.prop]" class="form-table-item checkbox"></el-checkbox>
  </el-form-item>
  <!-- checkbox -->
  <!-- switch -->
  <el-form-item v-else-if="isType('switch')" :label="item.label" :prop="item.prop">
    <el-switch v-model="model[item.prop]"></el-switch>
  </el-form-item>
  <!-- switch -->
  <!-- textarea -->
  <el-form-item v-else-if="isType(TYPE_STRING.TEXTAREA )" :label="item.label" :prop="item.prop">
    <el-input type="textarea" :rows="textareaRows" v-model="model[item.prop]"></el-input>
  </el-form-item>
  <!-- textarea -->
  <!-- date.picker -->
  <el-form-item v-else-if="isType(TYPE_STRING.DATE_PICKER)" :label="item.label" :prop="item.prop">
    <el-date-picker
      type="date"
      :placeholder="item.placeholder"
      v-model="model[item.prop]"
      value-format="yyyy-MM-dd"
      style="width: 100%;"
      value-format="yyyy-MM-dd"
    ></el-date-picker>
  </el-form-item>
  <!-- date.picker -->
  <!-- radio.group -->
  <el-form-item v-else-if="isType('radio.group')" :label="item.label" :prop="item.prop">
    <el-radio-group v-model="model.resource">
      <el-radio v-for="(item, index) in item.data" :key="index" :label="item.label"></el-radio>
    </el-radio-group>
  </el-form-item>
  <!-- radio.group -->
  <!-- checkbox.group -->
  <el-form-item v-else-if="isType('checkbox.group')" :label="item.label" :prop="item.prop">
    <el-checkbox-group v-model="model[item.prop]">
      <el-checkbox
        v-for="(item, index) in item.data"
        :key="index"
        :label="item.label"
        :name="item.value"
      ></el-checkbox>
    </el-checkbox-group>
  </el-form-item>
  <!-- checkbox.group -->
  <!-- multiple.date.picker -->
  <el-form-item v-else-if="isType('multiple.date.picker')" :label="item.label">
    <el-col :span="11">
      <el-form-item :prop="item.prop">
        <el-date-picker
          type="date"
          :placeholder="item.placeholder"
          v-model="model[item.prop]"
          style="width: 100%;"
        ></el-date-picker>
      </el-form-item>
    </el-col>
    <el-col class="line" :span="2">-</el-col>
    <el-col :span="11">
      <el-form-item :prop="tdS.items[1].prop">
        <el-time-picker
          type="fixed-time"
          :placeholder="tdS.items[1].placeholder"
          v-model="model[tdS.items[1].prop]"
          style="width: 100%;"
        ></el-time-picker>
      </el-form-item>
    </el-col>
  </el-form-item>
  <!-- multiple.date.picker -->
  <!-- empty -->
  <el-form-item v-else>
    <slot>{{item.label}}</slot>
  </el-form-item>
  <!-- empty -->
</template>

